mit"]){padding:0.5vw;width:17vw;margin-bottom:1vw;font-size:0.8vw;border-radius:5px;border:2px solid lightblue}.auth__container:first-child form input[type="submit"]{cursor:pointer;background-color:#1e90ff;color:#fff;border-radius:7px;padding:0.6vw 8vw;font-size:0.8vw;border:1px solid rgba(0,0,0,0)}.auth__container:first-child form input[type="submit"]:active{-webkit-transform:scale(0.98);transform:scale(0.98)}.auth__container{-webkit-flex:1 1 50%;flex:1 1 50%}.auth__change{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;position:absolute;bottom:1.5vw;font-size:0.8vw}.auth__change b{font-size:bold;color:#1e90ff;text-decoration:underline;-webkit-text-decoration-color:#1e90ff;text-decoration-color:#1e90ff;cursor:pointer}.auth__container:last-child{border-top-right-radius:10px;border-bottom-right-radius:10px;background-color:#1e90ff;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.auth__content{font-size:1vw;line-height:1.1vw;text-align:center;color:#fff;width:50%}.auth__content h1{font-size:2vw;padding:1vw;line-height:2.2vw}*{font-family:'Work Sans', sans-serif !important}

